.gform_body {
    input {
        border: 1px solid #ccc;
    }
}
.g-recaptcha {
    margin: 20px 0;
}
.hidden {
    display: none;
}
.show {
    display: block;
}
tr.text-line {
    margin: 0 0 10px 0;
    float: right;
    width: auto;
    clear: both;
    td input {
        padding-left: 10px;
    }
}
.sociallinks {
    width: 100%;
    margin: 10px 0;
}
section.team {
	width: 100%;

	.team-member {
		width: 100%;
		position: relative;
		float: left;

		@media screen and (min-width: $tabwidth) {
			width: 23%;
		}

		&.first {
			margin: 0 0% 20px 0;
			@media screen and (min-width: $tabwidth) {
				margin: 0 1.5% 20px 0;
			}
		}
		&.last {
			margin: 0 0% 20px 0;
			@media screen and (min-width: $tabwidth) {
				// margin: 0 0% 20px 0;
			}
		}
		.link a {
			width: 100%;
			height: 100%;
			position: absolute;
			display: block;
			top: 0;
			left: 0;
		}
		&:hover {
			.info {
				background-color: #0999F3;
			}
			.photo .overlay {
				opacity: 1;
			}
		}
		.photo {
			width: 100%;
			position: relative;
			float: left;
			line-height: 0;

			img {
				width: 100%;
				height: auto;
			}

			.overlay {
				width: 100%;
				height: 100%;
				background-color: rgba(0,0,0,.5);
				color: #fff;
				position: absolute;
				top: 0;
				left: 0;
				opacity: 0;
				@include transition(all ease .3s);
			}
			.plus {
				width: 100%;
				position: absolute;
				top: 50%;
				left: 0;
				text-align: center;
				font-size: 26px;
			}
		} // photo
	} // team member
	
	.info {
		width: 90%;
		float: left;
		position: relative;
		background-color: #8961AF;
		color: #fff;
		overflow: hidden;
		padding: 10px 5% 0 5%;
		@include transition(all ease .3s);
		background: {
			image: url(images/team-info.png);
			repeat: no-repeat;
			position: bottom -20px right -40px;
		}
	}
	h2 {
		font-size: 18px;
		margin: 0 0 8px 0;
		color: #fff;
	}
	h3 {
		font-size: 13px;
		margin: 0 0 5px 0;
		color: #fff;
	}
	
	
}
section.single-team {
	width: 100%;
	@media screen and (min-width: $tabwidth) {
		width: 90%;
		margin: 0 auto;
	}
	.single-photo {
		float: right;
		width: 100%;
		img {
			width: 100%;
			height: auto;
		}
		@media screen and (min-width: $tabwidth) {
			width: 26%;
		}
	}
	.bio {
		float: left;
		width: 100%;
		@media screen and (min-width: $tabwidth) {
			width: 70%;
		}
        h2 {
            margin: 0 0 0px 0;
        }
	}
    .clear {clear: both;}
    .team-button {
        width: auto;
        display: inline-block;
        clear: both;
        float: left;

        a {
            background-color: #8961AF;
            border-radius: 10px 10px 10px 10px;
            width: auto;
            display: inline-block;
            padding: 10px 20px;
            color: #fff;
            clear: both;
            text-decoration: none;
        }
    }
}

section.board-of-dir {
    width: 100%;
    clear: both;
    float: left;
    margin: 30px 0 50px 0;
}
